import { mapMutations } from 'vuex'
export default {
	methods: {
		// 监听进入地址管理
		NavToPath(){
			uni.navigateTo({
				url:'../MyPathConfig/MyPathConfig'
			})
		},
		// 点击退出登录
		OutLogin(){
			this.outLogin()
			uni.showToast({
				title:"退出成功"
			})
			uni.reLaunch({
				url:"../index/index"
			})
		},
		...mapMutations(['outLogin'])
	}
}
